.tooltip {
    cursor: help;
    position: relative;
}
  
.tooltip .tooltip-content {
    visibility: hidden;
    width: 110px;
    background-color: rgba(255, 255, 255, 0);
    color: rgb(44, 44, 44);
    text-align: center;
    border-radius: 6px;
    padding: 2px 0;
    position: absolute;
    z-index: 1;
    bottom: 150%;
    left: 50%;
    margin-left: -135px;

    opacity: 0;

    transform: translate(-8px, -4px);
    -webkit-transform: translate(-8px, -4px);

    transition: 0.15s;
    -webkit-transition: 0.15s;
}

.tooltip:hover .tooltip-content {
    visibility: visible;
    opacity: 255;

    transform: translate(-4px, 0px);
    -webkit-transform: translate(-4px, 0px);

    transition: 0.3s;
    -webkit-transition: 0.3s;
}

.debug-outline {
    outline: 1px;
